import { Injectable } from '@angular/core';
import { Place } from './place.model';

@Injectable({
  providedIn: 'root'
})
export class PlacesService {
  private _places: Place[] = [
    new Place('p1',
    'Manhattan Mansion', 
    'In the heart of New York City.',
    'https://assets.londonist.com/uploads/2012/01/i730/ngowerst_7.png',
    149.99),
    new Place('p2',
    'L\'Amour Toujours',
     'A romantic place in Paris!',
     'https://www.hotelnewsnow.com/media/Image/locations/ScribeHotel.jpg',
     189.99),
     new Place('p3',
    'The Foggy Palace',
     'Not your average city trip!',
     'https://cdn.fodors.com/ee/files/slideshows/11950/palace-hotels-india-hero.jpg',
     99.99)
  ];

  get places(){
    return [...this._places];
  }
  constructor() { }

  getPlace(id:string){
    return {...this._places.find(p=>p.id===id)};
  }
}
